.bg{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
  display: grid;
  grid-template-columns: 1fr 2fr;
  justify-content: center;
  align-items: center;

  .left{
    text-align: center;

    h1{
      letter-spacing: 1px;
      word-spacing: 1px;
      color: #4109C2;
      border-bottom: 2px solid black;
      padding-left: 10px;
      margin-left: 5px;
    }

    div{
      margin-top: 20px;
      display: flex;
      flex-wrap: wrap;
      padding-left: 10px;
      margin-left: 5px;
      justify-content: center;
      align-items: center;
      row-gap: 8px;
      column-gap: 8px;

      button{
        padding: 8px; 
        // margin: 5px;
        font-size: 16px;
        font-weight: 600;
        border-radius: 3px;
        border: none;
        text-transform: uppercase;
        letter-spacing: 1.1px;
      }
    }
  }

}

.rotating-circle {
  height: calc(100vh - 60px);
  margin: 0 auto;
  // background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 16cm; /* Double the radius to set the diameter of the circle */
  width: 16cm; /* Double the radius to set the diameter of the circle */
}

.circle {
  position: relative;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  // background-color: lightgray;
  overflow: hidden;
}

.image {
  position: absolute;
  top: 20%;
  left: 20%;
  transform: translate(-50%, -50%);
  height: 10cm; /* Adjust the size of the images */
  width: 10cm; /* Adjust the size of the images */
  // background-color: white;
  border-radius: 50%;
}
