.contact{
  width: 100%;
  height: calc(100vh - 60px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  background-image: linear-gradient(to right top, #ffffff, #f3f5fd, #e2edfc, #cde5f8, #b4dff2);

  .contImg{
    padding: 20px;

    img{
      width: 100%;
      height: 100%;
      background-size: contain;
    }
  }

  .data{
    width: 100%;
    height: 100%;
    // padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    // background-color: rgba(192, 142, 84, 0.445);

    form{

      label{
        font-size: 20px;
        font-weight: 500;
      }

      input{
        margin-bottom: 10px;
        padding: 5px 8px;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;

        #quote{
          padding: 20px;
        }
      }

      button{
        margin-top: 20px;
        background-color: rgba(109, 75, 36, 0.445);
        padding: 6px 10px;
        border: none;
        border-radius: 5px;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.5s;
        color: white;
        

        &:hover{
          background-color: rgba(70, 46, 19, 0.774);
        }
      }
    }
  }
}