
*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

:root{
  --mainColor: #29335C;
  --mainColorLight: #5767aa;
  --secondaryColor: #D82839;
  --textColor: #eee;
}

header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 2rem;
  background-color: var(--mainColor);;
  color: var(--textColor);

  h3{
    color: #eee;
    font-weight: 500;
  }
}



nav a{
  margin: 0 2rem;
  color: var(--textColor);
  text-decoration: none;
}

// nav a:hover{
//   color: var(--secondaryColor);
// }

header .nav-btn{
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
  font-weight: 400;
  color: white;
}

@media screen  and (max-width:777px){
  header .nav-btn{
    visibility: visible;
    opacity: 1;
  }

  header nav{
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    background-color: var(--mainColor);
    transition: 1s;
    transform: translateY(-100vh);
  }

  header .responsive_nav{
    transform: none;
  }

  nav .nav-close-btn{
    position: absolute;
    top: 2rem;
    right: 2rem;

  }

  nav a{
    font-size: 1rem;
  }

}
