@media screen and (max-width: 838px) {
  nav {
    a {
      h1 {
        margin-left: 50px;
      }
    }

    div {
      column-gap: 5px;
      a {
        font-size: 16px;
      }
    }
  }

  .bg {
    grid-template-columns: 1fr 1fr;

    .rotating-circle {
      height: 12cm;
      width: 12cm;
    }

    .image {
      top: 15%;
      left: 15%;
      height: 8cm;
      width: 8cm;

      img {
        width: 20;
      }
    }
  }
}

@media screen and (max-width: 582px) {
  nav {
    height: 70px;
    grid-template-columns: 1fr;
    // align-items: center;

    a {
      h1 {
        margin-left: 10px;
        font-size: 28px;
      }
    }

    div {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }

  .img {
    h1 {
      top: 60%;
      left: 30px;
    }
  }

  .bg {
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;

    .left {
      grid-row: 2;
    }
  }

  .resume {
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: start;

    .button {
      padding-left: 20px;
    }
  }

  .contact {
    grid-template-columns: 1fr;

    .contImg {
      width: 80%;
    }
  }
}

@media screen and (max-width: 450px) {
  .bg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

    .rotating-circle {
      height: 8cm; /* Double the radius to set the diameter of the circle */
      width: 8cm; /* Double the radius to set the diameter of the circle */
    }

    .image {
      position: absolute;
      top: 12%;
      left: 12%;
      transform: translate(-50%, -50%);
      height: 6cm; /* Adjust the size of the images */
      width: 6cm; /* Adjust the size of the images */
    }
  }

  .image-container {
    img {
      width: 90%;
    }
  }

  .detail {
    .detailChild {
      width: 80%;
    }
  }

  .contact {
    .contImg {
      padding: 20px;

      img {
        width: 100%;
        height: 100%;
        background-size: contain;
      }
    }

    .data {
      width: 100%;
      height: 100%;
      // padding: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      // background-color: rgba(192, 142, 84, 0.445);

      form {
        margin-left: 10px;

        label {
          font-size: 20px;
          font-weight: 500;
        }

        input {
          width: 90%;
          margin-bottom: 10px;
          // padding: 5px 8px;
          // font-size: 14px;
          display: flex;
          align-items: center;
          justify-content: center;
          border: none;

          #quote {
            padding: 20px;
          }
        }

        button {
          margin-top: 20px;
          background-color: rgba(109, 75, 36, 0.445);
          padding: 6px 10px;
          border: none;
          border-radius: 5px;
          font-size: 16px;
          font-weight: 500;
          cursor: pointer;
          transition: all 0.5s;
          color: white;

          &:hover {
            background-color: rgba(70, 46, 19, 0.774);
          }
        }
      }
    }
  }

  .resume {
    .aside {
      div {
        .heading {
          h1 {
            font-size: 18px;
          }

          #myImage1,
          #myImage2,
          #myImage3,
          #myImage4,
          #myImage5 {
            transition: transform 0.3s ease;
          }
          .rotated1,
          .rotated2,
          .rotated3,
          .rotated4,
          .rotated5 {
            transform: rotate(180deg);
          }
        }

        section {
          margin: 10px;

          div {
            margin: 5px;

            div {
              h2 {
                font-size: 14px;
              }

              p {
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
}
