.resume {
  width: 100%;
  height: calc(100vh - 60px);
  background-image: linear-gradient(to right top, #161615, #40413d, #70716b, #a3a59c, #d9ddd0);
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  justify-items: center;

  .button {
    h1 {
      color: #d3d9e7;
      letter-spacing: 1px;
    }
    a {
      button {
        margin-top: 10px;
        padding: 10px;
        border: none;
        cursor: pointer;
        font-weight: 500;
      }
    }
  }

  .aside {
    justify-self: self-start;
    width: 90%;
    div {
      margin: 10px;

      .heading {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 2px solid black;
        cursor: pointer;
        // background-color: rgb(46, 46, 46);

        h1 {
          text-transform: uppercase;
          font-size: 24px;
          color: black;
        }

        #myImage1, #myImage2, #myImage3, #myImage4, #myImage5 {
          transition: transform 0.3s ease;
        }
        .rotated1, .rotated2, .rotated3, .rotated4, .rotated5{
          transform: rotate(180deg);
        }
      }

      section {
        margin: 10px;

        div {
          margin: 5px;

          div {
            display: flex;
            justify-content: space-between;

            h2 {
              font-size: 17px;
            }

            p {
              font-size: 15px;
            }
          }
        }

        ul {
          margin-left: 25px;
        }

        .flex {
          display: flex;
          flex-wrap: wrap;
          column-gap: 20px;
          row-gap: 5px;
        }

        button {
          padding: 10px;
          border: none;
          cursor: pointer;
          font-weight: 500;
        }
      }
    }
  }
}
